<template>
  <div>

    <div style="margin-top: 10px;display: flex;justify-content: center">
        <el-input v-model="keywords" placeholder="通过用户名搜索用户..." prefix-icon="el-icon-search"
                  style="width: 400px;margin-right: 10px" @input="doSearch"
        ></el-input>
        <el-button icon="el-icon-search" type="primary" @click="doSearch">搜索</el-button>
    </div>

    <div class="hr-container">
      <el-card class="hr-card" v-for="(hr,index) in hrs" :key="index">
        <div slot="header" class="clearfix">
          <span>{{hr.name}}</span>
          <el-button style="float: right; padding: 3px 0;color:red;" type="text" icon="el-icon-delete" @click="doDelete(hr)"
          ></el-button>
        </div>
        <div>
          <div class="img-container">
            <img :src="hr.userface" :alt="hr.name" :title="hr.name" class="userface-img">
          </div>

          <div class="userinfo-container">
              <div>用户名: {{hr.name}}</div>
              <div>手机号码: {{hr.phone}}</div>
              <div>电话号码: {{hr.telephone}}</div>
              <div>地址: {{hr.address}}</div>
              <!--在这个按钮中，只要改变了状态，会被反应到hr对象中。因为数据已经双向绑定了。那么，我们就可以通过这个hr对象作为参数，来向后端发送请求。-->
              <div>用户状态: <el-switch
                              v-model="hr.enabled"
                              active-text="启用"
                              @change="enabledChange(hr)"
                              active-color="#13ce66"
                              inactive-color="#ff4949"
                              inactive-text="禁用">
                            </el-switch>
              </div>
              <div>用户角色：
                <el-tag type="success" style="margin-right: 4px" v-for="(role,indexj) in hr.roles" :key="indexj">{{role.nameZh}}</el-tag>
                <el-popover
                    placement="right"
                    title="角色列表"
                    width="200"
                    @show="showPop(hr)"
                    @hide="hidePop(hr)"
                    trigger="click">
                  <el-select v-model="selectedRoles" multiple placeholder="请选择">
                    <el-option
                        v-for="(r,indexk) in allroles"
                        :key="indexk"
                        :label="r.nameZh"
                        :value="r.id">
                    </el-option>
                  </el-select>
                  <el-button icon="el-icon-more" slot="reference" type="text"></el-button>
                </el-popover>
              </div>
              <div>备注：{{hr.remark}}</div>
          </div>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
export default {
  name: "SysHr",
  data(){
    return {
      //搜索框。
      keywords:'',
      //存放所有hr的信息。
      hrs:[],
      //显示hr已经有的角色。用来在显示下拉框的时候预选中。
      selectedRoles:[],
      //用来存放数据库中所有的角色。所有的角色都会被请求到，然后放在这个数组中。
      allroles:[],
      //用来临时存放一下数据，用来比较用户角色是否发生了改变。在hidePop的时候，不一定每一次都要更新用户角色的。
      needUpdate:[]
    }
  },
  mounted() {
    this.initHrs();
  },
  methods:{
    doDelete(hr){
      console.log(hr.id);
      this.$confirm('此操作将永久删除 '+hr.name+' 管理员, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
       this.deleteRequest("/system/hr/?hrid="+hr.id).then(resp=>{
         if (resp){
           this.initHrs();
         }
       })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    doSearch(){
      this.initHrs();
    },
    hidePop(hr){
      let roles = hr.roles;
      let flag = false;

      if (roles.length != this.selectedRoles.length){
        //当两个长度不相等的时候，一定需要更新。
        flag = true;
      }else {
        //当两个长度相等，还需要判断数组里面的每一项是否相等。
        this.needUpdate = [];
        roles.forEach(r => {
          this.needUpdate.push(r.id);
        })

        if (this.needUpdate.sort().toString() != this.selectedRoles.sort().toString()){
          flag = true;
        }
      }
      //只有当flag置位true的时候，才去更新操作。
      if (flag){
        let url = '/system/hr/role?hrid='+hr.id;
        this.selectedRoles.forEach(sr=>{
          url += '&rids='+sr;
        })
        this.putRequest(url).then(resp=>{
          if (resp){
            this.initHrs();
          }
        })
      }

    },
    showPop(hr){
      this.initAllRoles();
      let roles = hr.roles;
      this.selectedRoles = [];
      roles.forEach(item=>{
        this.selectedRoles.push(item.id);
      })
    },
    enabledChange(hr){
      delete hr.roles;
      this.putRequest("/system/hr/",hr).then(resp=>{
        if (resp){
          this.initHrs();
        }
      })
    },
    initAllRoles(){
      this.getRequest("/system/hr/roles").then(resp=>{
        if (resp){
          this.allroles = resp;
        }
      })
    },
    initHrs(){
      this.getRequest("/system/hr/?keywords="+this.keywords).then(resp=>{
        if (resp){
          this.hrs = resp;
        }
      })
    }
  }
}
</script>

<style>
  .userinfo-container{
    font-size: 12px;
    color: #049eff;
  }
  .userinfo-container{
    margin-top: 20px;

  }
  .img-container{
    width: 100%;
    display: flex;
    justify-content: center;
  }
  .userface-img{
    width: 72px;
    height: 72px;
    border-radius: 72px;
  }
  .hr-card{
    width: 350px;
    margin-bottom: 20px;
  }
  .hr-container{
    margin-top: 10px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }
</style>