<template>
  <div style="width: 570px;">
    <el-input
        prefix-icon="el-icon-search"
        placeholder="输入部门名称进行搜索"
        v-model="filterText">
    </el-input>

    <el-tree
        :data="deps"
        :props="defaultProps"
        :expand-on-click-node="false"
        :filter-node-method="filterNode"
        ref="tree">

       <span style="display: flex;justify-content: space-between;width: 100%" class="custom-tree-node" slot-scope="{ node, data }">
        <span style="font-size: 16px">{{ data.name }}</span>
        <span>
          <el-button
              type="success"
              size="mini"
              class="depBtn"
          >
          查看员工
          </el-button>
          <el-button
              type="primary"
              size="mini"
              class="depBtn"
              @click="() => showAddDepView(data)">
            添加部门
          </el-button>
          <el-button
              type="danger"
              size="mini"
              class="depBtn"
              @click="() => deleteDep(data)">
            删除部门
          </el-button>
        </span>
      </span>
    </el-tree>
    <el-dialog
        title="添加部门"
        :visible.sync="dialogVisible"
        width="30%"
        >
      <div>
        <table>
          <tr>
            <td><el-tag>上级部门</el-tag></td>
            <td>{{pname}}</td>
          </tr>

          <tr>
            <td><el-tag>部门名称</el-tag></td>
            <td><el-input v-model="dep.name" placeholder="请输入部门名称"></el-input></td>
          </tr>
        </table>
      </div>

      <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="doAddDep">确 定</el-button>
  </span>
    </el-dialog>

  </div>
</template>

<script>
export default {
  name: "DepMana",
  watch: {
    filterText(val) {
      this.$refs.tree.filter(val);
    }
  },
  data(){
    return{
      filterText:'',
      deps:[],
      dep:{
        name:'',
        parentId:-1
      },
      pname:'',
      dialogVisible:false,
      defaultProps: {
        children: 'children',
        label: 'name'
      }
    }
  },
  mounted() {
    this.initDeps();
  },
  methods:{
    initDep(){
      this.dep = {
        name:'',
        parentId:-1
      },

      this.pname = '';
    },
    addDep2Deps(deps,dep){
      for (let i = 0; i < deps.length; i++) {
        let d = deps[i];
        if (d.id == dep.parentId){
          d.children = d.children.concat(dep);
          if (d.children.length > 0){
            d.parent = true;
          }
          return ;
        }else {
          this.addDep2Deps(d.children,dep);
        }
      }
    },
    //动态的从已经请求到的deps中删除一项。因为deps是一个数组。所以可以递归的删除。
    removeDepFromDeps(p,deps,id){
      for (let i = 0; i < deps.length; i++) {
        let d = deps[i];
        if (d.id == id){
          deps.splice(i,1);
          if (deps.length == 0){
            p.isParent = false;
          }
          return ;
        }else{
          this.removeDepFromDeps(d,d.children,id);
        }
      }
    },
    doAddDep(){
      this.postRequest("/system/basic/department/",this.dep).then(resp=>{
        if (resp){
          this.addDep2Deps(this.deps,resp.obj);
          this.dialogVisible = false;
          this.initDep();
        }
      })
    },
    deleteDep(data){
      if (data.isParent){
        this.$message.error("该部门有子部门，删除失败！");
      }else{
        this.$confirm('此操作将永久删除 '+data.name+' 部门, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.deleteRequest("/system/basic/department/" + data.id).then(resp=>{
            if (resp){
                //当删除成功的时候，并不需要重新去请求数据，我们只需要从已经请求到的数据中将要删除的那一项动态的删除就行了。
                //因为当动态的请求的话，在删除成功之后，已经展开的那一项会被折叠。
                this.removeDepFromDeps(null,this.deps,data.id);
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      }
    },
    showAddDepView(data){
      this.pname = data.name;
      this.dep.parentId = data.id;
      this.dialogVisible = true;
    },
    initDeps() {
      this.getRequest("/system/basic/department/").then(resp=>{
        if (resp){
          this.deps = resp;
          console.log(">>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>")
          console.log(this.deps)
        }
      })
    },
    //这个方法是当搜索框中的值发生变化的时候回调用的方法。
    filterNode(value, data) {
      if (!value) return true;
      return data.name.indexOf(value) !== -1;
    }
  }
}
</script>

<style>
  .depBtn{
    padding: 2px ;
  }
</style>